import './App.scss';
import React, { useEffect, useState } from 'react';
import Body from './components/Body';
import { Card } from 'react-bootstrap';
import {
  BrowserRouter as Router,
  Routes,
  Route
} from "react-router-dom";
import PairingTool from './components/PairingTool/PairingTool';

const MAIN_COLOR = '#e7154e'
const SECONDARY_COLOR = '#fce3ee'
export const MAIN_COLOR_DEP = '#80183b'

function App() {
  const [width, setWidth] = useState(window.innerWidth);

  useEffect(() => {
    console.log(window.location)

    window.addEventListener('resize', handleWindowSizeChange);
    return () => {
      window.removeEventListener('resize', handleWindowSizeChange);
    }
  }, [])

  let isMobile = (width < 768);
  let isTablet = (width >= 768 && width < 1024)
  let vh = window.innerHeight * 0.01;
  document.documentElement.style.setProperty('--vh', `${vh}px`);

  function handleWindowSizeChange() {
    setWidth(window.innerWidth);
  }

  let mobileStyle = {
    width: '100%',
    height: 'calc(var(--vh, 1vh) * 100 - 100px)',
    borderWidth: '0px',
    padding: '2px'
  }
  let tabletStyle = {
    width: '80%',
    height: 'calc(var(--vh, 1vh) * 100 - 120px)',
    borderWidth: '0px',
    padding: '10px'
  }
  let desktopStyle = {
    marginTop: '100px',
    width: '500px',
    padding: '10px'
  }

  const getDeviceStyle = () => {
    if (isMobile)
      return mobileStyle
    if (isTablet)
      return tabletStyle
    return desktopStyle
  }

  return (
    <div className="App">
      <div id="body-logo"
        style={{
          // marginBottom: isMobile ? '20px' : '40px',
          height: '80px',
          backgroundColor: MAIN_COLOR_DEP,
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
        }}
        onClick={e => { window.location.replace('/Roznavene-project-demo') }}
      >
        <img
          style={{ objectFit: 'cover' }}
          href="/Roznavene-project-demo"
          className="clickable nodrag"
          src="/Roznavene-project-demo/PocketSomm.LOGO.svg"
          width="200px"
          height="80px">
        </img>
      </div>
      <Router>
        <Routes>
          <Route path="/Roznavene-project-demo/" element={
            <Card
              className="mainBody"
              style={{
                marginTop: isMobile ? '20px' : '40px',
                ...getDeviceStyle()
              }}>
              <Body isMobile={isMobile || isTablet} screenWidth={width} />
            </Card>
          } />

          <Route path="/Roznavene-project-demo/pairing" element={
            <PairingTool />
          }>
          </Route>
        </Routes>
      </Router>
    </div>
  );
}

export default App;
