import './PairingTool.scss';
import React from 'react';
import { Form, Modal, Button, ListGroup, Toast } from 'react-bootstrap';
import axios from 'axios';
import { BsXLg } from "react-icons/bs";
import Tag from './Tag';
import Loadable from '../Loadable';

class PairingTool extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            wineLinks: '',
            loading: true,
            recipe: {
                title: 'Braised Green Beans with Fried Tofu',
                labels: ['Pork', 'Vegetables'],
                regions: [],
                ingredients: [
                    '2 tablespoons white sugar',
                    '3 tablespoons soy sauce',
                    '1 cup dry white wine',
                    '1/2 cup chicken broth',
                    '1 package tofu , drained',
                    'salt and pepper to taste',
                    '1 tablespoon cornstarch',
                    '3 cups oil for frying , or as needed',
                    '1 onion , chopped',
                    '4 plum tomatoes , sliced into thin wedges',
                    '12 ounces fresh green beans , trimmed and cut into 3 inch pieces',
                    '1 cup bamboo shoots , drained and sliced',
                    '1 cup chicken broth , or as needed',
                    '2 tablespoons cornstarch',
                    '3 tablespoons water'
                ],
                steps: "1.\tIn a small bowl, stir together the white sugar, soy sauce, white wine and 1/2 cup of chicken broth. Set the sauce aside. | 2.\tPat the tofu dry with paper towels, and cut into cubes. Season the cubes with salt and pepper. Sprinkle 1 tablespoon of cornstarch over them on all sides. | 3.\tHeat a little more than 1 inch of oil in a large deep skillet over medium-high heat. If you have a deep-fryer, fill to the recommended level, and heat the oil to 375 degrees F (190 degrees C). When the oil is hot, add the tofu, and fry until golden brown on all sides. Turn occasionally. Remove from the oil with a slotted spoon, and drain on paper towels. | 4.\tIn a separate skillet, heat one tablespoon of oil over medium-high heat. Add the onions and green beans; cook and stir for 3 to 5 minutes. Season with salt and pepper. Stir in the tomatoes, and cook until they begin to break apart, about 4 minutes. Add the bamboo shoots, and stir to blend. | 5.\tStir the sauce into the skillet with the beans, and bring to a boil. Cook for 5 minutes, stirring occasionally. If the liquid starts to evaporate too much, stir in up to 1 cup of chicken broth. | 6.\tMix together the remaining 2 tablespoons of cornstarch and water until cornstarch is dissolved. Stir this into the sauce in the skillet. Simmer, stirring gently, until the sauce clears and thickens. Add the fried tofu, and stir to coat with the sauce."
            },
            show: false,
            toastShow: false,
            toastContent: ''
        }
    }


    handleClose = () => this.setState({ show: false })
    handleShow = () => this.setState({ show: true })

    handleCreateToast = (title) => {
        this.setState({ toast: title })
    }

    isUrl(text) {
        var expression = /[-a-zA-Z0-9@:%._\+~#=]{1,256}\.[a-zA-Z0-9()]{1,6}\b([-a-zA-Z0-9()@:%_\+.~#?&//=]*)?/gi;
        var regex = new RegExp(expression)

        if (text.match(regex)) {
            return true
        } else
            return false
    }

    submitRecipe() {
        const title = this.state.recipe.title

        const options = {
            method: 'POST',
            url: 'https://pocketsomm.dev/api/v1/recipePairing',
            headers: { 'Content-Type': 'application/json' },
            data: {
                wines: this.listOfUrls(this.state.wineLinks).map(l => ({ link: l, info: '' })),
                recipeId: this.state.recipe.id,
                recipeTitle: this.state.recipe.title
            }
        };

        this.setState({ loading: true })

        axios.request(options).then(res => {
            console.log(res.data);
            this.setState({ toastShow: true, toastContent: `Pairing of ${title} successful.` })
            this.nextRecipe()

        }).catch(function (error) {
            console.error(error);
        });
    }

    nextRecipe() {
        this.setState({ loading: true, wineLinks: '' })
        axios.get('https://pocketsomm.dev/api/v1/randomRecipe').then(res => {
            this.setState({
                loading: false,
                recipe: {
                    id: res.data.Recipe_id,
                    title: res.data.Recipe_title,
                    labels: [],
                    regions: Object.entries(res.data.Cuisine)
                        .sort(([, a], [, b]) => b - a).slice(0, 3).map(e => `${e[0]} ${Math.round(e[1] * 100)}%`),
                    labels: res.data.Category ? res.data.Category.map(c => c.replace(/(^\w{1})|(\s+\w{1})/g, letter => letter.toUpperCase())) : [],
                    ingredients: res.data.Ingredients.slice(2, -2).split("', '"),
                    steps: res.data.Steps
                }
            })
        })
    }

    componentDidMount() {
        this.nextRecipe()
    }

    handleTextChange = text => {
        this.setState({ wineLinks: text })
    }

    listOfUrls = (text) => {
        return text.split(/\r?\n/).filter(l => this.isUrl(l))
    }

    render() {
        return (
            <Loadable style={{ marginTop: '300px' }} loading={this.state.loading} component={
                <div style={{ width: '100%', height: '800px' }}>
                    <Modal scrollable centered size="lg" show={this.state.show} onHide={this.handleClose}>
                        <Modal.Header>
                            <Modal.Title>{this.state.recipe.title}</Modal.Title>
                        </Modal.Header>
                        <Modal.Body>
                            <ListGroup variant="flush">
                                {this.state.recipe.steps.split(/\| [0-9]+\./).splice(1).map((step, i) =>
                                    <ListGroup.Item>
                                        <span style={{ display: 'block', marginBottom: '10px' }}>
                                            <b>{i + 1}.</b> {step}
                                        </span>
                                    </ListGroup.Item>
                                )}
                            </ListGroup>
                        </Modal.Body>
                        <Modal.Footer>
                            <Button variant="danger" onClick={this.handleClose}>
                                Close
                            </Button>
                        </Modal.Footer>
                    </Modal>

                    <Toast
                        onClose={() => this.setState({ toastShow: false })}
                        delay={8000}
                        animation
                        autohide
                        show={this.state.toastShow}
                        style={{
                            position: 'absolute', right: '50px', top: '50px'
                        }}
                    >
                        <Toast.Header>
                            <strong className="me-auto">PocketSomm</strong>
                        </Toast.Header>
                        <Toast.Body>{this.state.toastContent}</Toast.Body>
                    </Toast>



                    <div style={{ width: '100%', height: 'calc((100vh - 80px)*0.7)', backgroundColor: '#f0f0f0', paddingLeft: '30px', paddingRight: '30px' }}>
                        <div style={{ width: '100%', height: '180px', paddingTop: '10px' }}>
                            <h4>{this.state.recipe.title}</h4>
                            <button class="btn btn-link" onClick={this.handleShow}>View steps</button>
                            <div style={{ width: '100%', textAlign: 'start', height: '32px', display: 'flex', alignItems: 'center' }}>
                                Predicted Regions: {this.state.recipe.regions.length ? this.state.recipe.regions.map(r =>
                                    <Tag content={`${r}`} declinable={true} />
                                ) : <span style={{ marginLeft: '30px', fontSize: '14px', color: 'darkgray' }}>None</span>}
                            </div>
                            <div style={{ width: '100%', textAlign: 'start', height: '32px', display: 'flex', alignItems: 'center' }}>
                                Predicted Categories: {this.state.recipe.labels.length ? this.state.recipe.labels.map(l =>
                                    <Tag content={`${l} `} declinable={true} />
                                ) : <span style={{ marginLeft: '30px', fontSize: '14px', color: 'darkgray' }}>None</span>}
                            </div>
                        </div>

                        <div style={{ width: '35%', textAlign: 'start', float: 'left', height: 'calc(100% - 190px)', overflowY: 'auto' }}>
                            {this.state.recipe.ingredients.map(i => <span style={{ display: 'block' }}>{i}</span>)}
                        </div>

                        {/* <div style={{ width: '65%', textAlign: 'start', float: 'left', height: '75%', overflowY: 'auto', backgroundColor: '#e8e8e8', padding: '10px' }}>
                        {this.state.recipe.steps.split(/\| [0-9]+\./).splice(1).map((step, i) =>
                            <span style={{ display: 'block', marginBottom: '10px' }}>
                                {i + 1}. {step}
                            </span>
                        )}
                    </div> */}
                        {/* <div style={{ width: '30%' }}>lel</div> */}
                    </div>
                    <div style={{ width: 'calc((100vh - 80px)*0.3)', height: '30%' }}>
                        <Form.Control
                            onChange={e => this.handleTextChange(e.target.value)}
                            value={this.state.wineLinks}
                            as="textarea"
                            placeholder="Paste wine links here (one per row)"
                            style={{ height: '190px', width: '700px', marginLeft: '40px', marginTop: '20px' }}
                        />
                    </div>
                    <div style={{ width: '100%', position: 'absolute', bottom: '0px', height: '50px' }}>
                        <button disabled={
                            !this.state.wineLinks.length || !this.isUrl(this.state.wineLinks.split('\n')[0])
                        } type="button" class="btn btn-danger" onClick={e => this.submitRecipe()}>Submit Pairings ({this.listOfUrls(this.state.wineLinks).length})</button>
                        <button type="button" class="btn btn-danger-outline" onClick={e => this.nextRecipe()}>Skip Recipe</button>
                    </div>
                </div>
            } />
        )
    }
}

export default PairingTool;